import React from "react"
import { IconContext } from "react-icons";
import { FaFacebook, FaTelegram } from "react-icons/fa";
import { FaInstagram } from "react-icons/fa";



const Header = () => {
  return (
    <>

      <div className="contact-section">
    
           
          
      <div className="icons-ca">
      <h1 className="head4 color-green ">Connect with me </h1>
              <IconContext.Provider value={{ color: "white", size: "1.5em" }}>
                <div><a href="https://www.facebook.com/roem.fegi" target="_blank" className="white"><FaFacebook />   Facebook</a></div>
                <div><a href="https://www.instagram.com/roemnant/" target="_blank" className="white"><FaInstagram />  Instagram</a></div>
                <div><a href="https://web.telegram.org/k/" target="_blank" className="white" ><FaTelegram />   Telegram</a></div>
              </IconContext.Provider>

            </div>

    </div>

    </>

  )
}

export default Header

