import React, { useEffect } from 'react'
import ReactDOM from 'react-dom'
import Header from './Header'
import { Container, Navbar } from 'react-bootstrap';
import { Row } from 'react-bootstrap';
import { Col } from 'react-bootstrap';
import em from "../pfl.jpg"
import Reading from './Reading';
import Writing from './Writing';
import Song from './Song';

const About = () => {
	return (
		<>
			<div className="about-section">
				<Container>
					<Row >
						<Col>
							<div className='image-col'>
								<center>
									<img src={em} width="350" className='my-img' />

									<div className="abt font-weight-bold">

									</div>
								</center>
							</div>
						</Col>
						<Col>
							<div className="about-me">
								<h1>
									ABOUT
								</h1>
								<div className="about-me container">
									<h4>Hi! This is Roem.  </h4>
								</div>
								<div className="about-me container">
									<h4>A 21 year-old, BS Information Technology student at Caraga State University
										- Main campus, Ampayon, Butuan City. I adore journaling and reading the Bible on a daily basis.
										as well as spend time with God in prayer. I do love talking to people who are
										interested in productivity and self-improvement. I enjoy preaching God's Word and
										I like also to get out with my buddies and play Christian music with them. </h4>
								</div>
								<div className='Likes'>
								</div>
							</div>
						</Col>
					</Row>
				</Container>
				<div className='lottiess' >
					<div style={{width: '20rem'}}><Reading/></div>
					<div style={{width: '20rem'}}><Song/></div>
					<div style={{width: '20rem'}}><Writing/></div>
					
				</div>
			</div>
		</>)
}

const AboutTitle = () => {
	// This effect runs once, after the first render
	useEffect(() => {
		document.title = "Roem Fegi"
	}, [])
};

ReactDOM.render(
	<AboutTitle />,
	document.getElementById('root')
);

export default About
