import React from 'react'
import Typed from "react-typed"

const Typewriting = () => {
  return (
    <div>
      <Typed
      className='type-txt'
      strings={["Roem Fegi"]}
      typeSpeed={40}
      backSpeed={60}
      loop
      />
    </div>
    
  )
}

export default Typewriting
