import React, { useEffect } from 'react'
import ReactDOM from 'react-dom'
import Header from './Header'


const Home = () => {
    return (
        <>
            <Header />
        </>
    )
}
const HomeTitle = () => {
    // This effect runs once, after the first render
    useEffect(() => {
      document.title = "Roem Fegi"
    }, [])
  };
  
  ReactDOM.render(
    <HomeTitle />,
    document.getElementById('root')
  );

export default Home
