import React, { useEffect } from 'react'
import ReactDOM from 'react-dom'
import './form.css'

export default function Hotel() {
  return (
    <>
    <div className=" text-center"><h2>Online Hotel Booking</h2></div>
    <div class="form-container">
      <form class="register-form">
        
        <input
          id="first-name"
          class="form-field"
          type="text"
          placeholder="First Name"
          name="firstName"
        />
        
        <input
          id="last-name"
          class="form-field"
          type="text"
          placeholder="Last Name"
          name="lastName"
        />
       
        <input
          id="email"
          class="form-field"
          type="text"
          placeholder="Email"
          name="email"
        />
        
        <input
          id="Check-in"
          class="form-field"
          type="text"
          placeholder="Check-in"
          name="Check-in"
        />
       
       <input
          id="Check-out"
          class="form-field"
          type="text"
          placeholder="Check-out"
          name="Check-out"
        />

      <input
          id="Guest"
          class="form-field"
          type="text"
          placeholder="Guest"
          name="Guest"
        />



        <button class="form-field" type="submit">
          Submit
        </button>
      </form>
      {/* <div><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#59e94a"
       fill-opacity="1" d="M0,32L60,69.3C120,107,240,181,360,218.7C480,256,600,256,720,234.7C840,213,960,
       171,1080,170.7C1200,171,1320,213,1380,234.7L1440,256L1440,320L1380,320C1320,320,1200,320,1080,320C960,
       320,840,320,720,320C600,320,480,320,360,320C240,320,120,320,60,320L0,320Z"></path></svg></div> */}
    </div>
    </>
  );
}

const HotelTitle = () => {
  // This effect runs once, after the first render
  useEffect(() => {
    document.title = "Roem Fegi"
  }, [])
  
};

ReactDOM.render(
  <HotelTitle />,
  document.getElementById('root')
);