import React from "react"
import Profile_img from "../em.png";
import { IconContext } from "react-icons";
import Typewriting from "./Typewriting";
import { FaFacebook, FaTelegram } from "react-icons/fa";
import { FaInstagram } from "react-icons/fa";



const Header = () => {
  return (
    <div className="main-section">


<div className="icons">
              <IconContext.Provider value={{ color: "black", size: "1.5em" }}>
                <div><a href="https://www.facebook.com/roem.fegi" target="_blank"><FaFacebook /></a></div>
                <div><a href="https://www.instagram.com/roemnant/" target="_blank"><FaInstagram /></a></div>
                <div><a href="https://web.telegram.org/k/" target="_blank"><FaTelegram /></a></div>
              </IconContext.Provider>

            </div>

      <div className="container-row">
        <div class="row">
          <div class="col">
          <h1 className="head2 ">HELLO, I'm <Typewriting/></h1>
          <h2 className="head3 color-rgba">I hope you're doing well!</h2>
         
          <div class="btn-lm">
            <a class="nav-link" href="/About">Learn More</a>
          </div>
          </div>

          

          <div class="col bg-lightblue">
            
            <img className="profile" src={Profile_img} width="400" />

           
          </div>

        </div>
      </div>
    </div>

  )
}

export default Header

